
/* SASS Style Theme --------------------------------------- */

$color-primary: #202020;
$banner-font: Brush Script MT;
$body-font: Brush Script MT;
$secondary-color: ;
$backgroundcolor: #F7F7F7;

/* Youtube Player Aspect Ratio Mixin --------------------------------------- */



/* Universal Page Properties --------------------------------------- */


body {
 margin-top: 0px;
 margin-right: 0px;
 margin-bottom: 0px;
 margin-left: 0px;
 }

/* Heading Banner ------------------------------------------- */

header {
  padding-top: 0px;
  margin-bottom: -250px;
  height: 700px;
  background: linear-gradient(to top, $backgroundcolor 0%, transparent 65%),
  linear-gradient(to top, $backgroundcolor 0%, transparent 10%),
  url('img/background.jpg') no-repeat center;
  background-size: cover;
}

h1, .intro {
  color: $color-primary;
  font-family: $banner-font;
  text-align: center;
}

h1 {
  margin: 0px;
  padding-top: 30px;
  font-size: 7em;
}

.intro {
  margin-top: 20px;
  margin-bottom: 40px;
  font-size: 4em;
}

.jp-audio{
  margin-top: 20px;
margin: auto;
}


/*Body Styler--------------------------------------------*/

h2{
  color: $color-primary;
  font-family: $body-font;
  font-size: 2em;
  text-align: center;
  text-decoration: underline;
  margin: 10px, 0px;
}


/*audio styler --------------------------------------------------------*/
.mainAudio {
    padding-top: 100px;
    margin: 10px auto; width: 320px;
    background: rgba(0, 0, 0, 0);
    position: center;
}

/*body content (blog/video - flex box) --------------------------------*/
.bodyContent {
  margin:20px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 2fr));
  grid-template-rows: 450px;
  grid-auto-rows: 450px;
  color: $color-primary;
}

.auto-resizable-iframe{
  width: 100%;
  margin-left: auto;
margin-right: auto;
height: 75%;
width: 90%;
object-fit: fill;
border-radius: 15px;
}

.blog{
  height:100%;
  width: 90%;
  border: 3px solid rgba($color-primary, 0.8);
  border-radius: 15px;
  margin-left: auto;
  margin-right: auto;
}


footer{
  width: 100%;
  margin: auto;
  background: linear-gradient(to top, darkgrey 0%, transparent 100%),
  no-repeat center;
  text-align: center;
  position: absolute;
     li {
       font-size: 75px;
       color: rgba($color-primary, 0.8);
       display: inline-block;
       border: none;
       cursor: pointer;
       margin: 20px;
       transition: ease 0.4s;
         &:hover {
           color: rgba($color-primary, 1);
      }
    }
  }

.social:hover > li {
  opacity: 1;
}

.social:hover > li:hover {
  opacity: 1;
}

body{
  background-color: $backgroundcolor;
}

/*media queries --------------------------------*/


@media (max-width: 600px) {
.bodyContent {

}
}

@media (min-width: 100px) {
.bodyContent {
grid-template-columns: repeat(auto-fit, minmax(450px, 2fr));
}
}
